.footer {
  //   height: 355px;
  // margin-top: 150px;
  background: #ecf0fd;
  padding: 0 100px;
  .top {
    display: flex;
    justify-content: space-between;
    h3 {
      font-family: Sen;
      font-style: normal;
      font-weight: bold;
      font-size: 34px;
      line-height: 41px;
      /* identical to box height */

      color: #7854f7;
    }
    .links {
      display: flex;
      justify-content: center;
      align-items: center;
      h5 {
        font-family: DM Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 29px;
        /* identical to box height */

        letter-spacing: -0.3px;

        color: #17141f;
      }
      a {
        margin-left: 20px;
        background: #7854f7;
        border-radius: 8px;
        text-decoration: none;
        color: white;
        padding: 10px 20px;
      }
    }
  }
  .body {
    margin: 20px 0;
    display: grid;
    grid-template-columns: 200px auto;

    .left {
      //   width: 200px;
      h5 {
        font-family: Circular Std;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 28px;
        letter-spacing: -0.3px;

        color: #17141f;
      }
      .form {
        display: flex;
        input {
          border: none;
          outline: none;
          background: transparent;
          border-bottom: 0.1px solid #7854f7;
          width: 100%;
          height: 30px;
          font-family: Circular Std;
          font-style: normal;
          font-weight: 450;
          font-size: 14px;
          // line-height: 19px;
          /* identical to box height */

          letter-spacing: -0.204545px;

          color: #17141f;

          mix-blend-mode: normal;
          opacity: 0.5;
        }
        .submit {
          background: #7854f7;
          border-radius: 8px 8px 0px 0px;
          width: 30px;
          color: white;
          //   display: flex;
          text-align: center;
        }
      }
    }
    .right {
      display: flex;
      justify-content: flex-end;

      flex-wrap: wrap;
      ul {
        list-style-type: none;
        // margin-left: 40px;
        width: 150px;
      }
      li {
        padding: 10px 0px;
        font-family: Circular Std;
        font-style: normal;
        font-weight: normal;
        line-height: 19px;
        /* identical to box height */

        letter-spacing: -0.204545px;

        color: #17141f;
        &:first-child {
          font-size: 18px;
          font-weight: bold;
        }
      }
    }
  }
  .bottom {
    display: flex;
    justify-content: space-between;
    .terms {
      ul {
        display: flex;
        padding: 0;
        list-style-type: none;
        li {
          padding-right: 20px;
          font-family: Circular Std;
          font-style: normal;
          font-weight: 450;
          font-size: 15px;
          line-height: 19px;
          /* identical to box height */

          letter-spacing: -0.204545px;

          color: #17141f;
        }
      }
    }
    .social {
      ul {
        display: flex;
        list-style-type: none;
        li {
          padding: 0 20px;
        }
      }
    }
  }
}
