.container {
  background: #ece8fd;
  margin-top: 200px;
  height: 500px;
  display: flex;
  // flex-wrap: wrap;
  justify-content: center;
  grid-gap: 30px;
  align-items: center;
  .card {
    width: 350px;
    margin-top: -200px;
    background: #ffffff;
    border-radius: 30px;
    padding: 20px;
    filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.1));
    .imgTaker {
      //   background: #c4c4c4;
      border-radius: 30px;
      overflow: hidden;
      // width: 300px;
      img {
        width: 100%;
      }
    }
    .text {
      h3 {
        font-family: Circular Std;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 20px;
        /* identical to box height */
        // margin: ;
        color: #5243c2;
      }
      p {
        font-family: Circular Std;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 25px;
        /* or 162% */

        color: #17141f;
      }
    }
    .links {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      a {
        font-family: Circular Std;
        font-style: normal;
        font-weight: bold;
        line-height: 140.1%;
        /* or 29px */

        color: #5243c2;
        text-decoration: none;
      }
      .fees {
        background: #eef7fe;
        border-radius: 5px;
        padding: 5px 10px;
      }
    }
  }
}
