/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
  .container {
    // display: grid;
    // grid-template-columns: 50px 50px;
    .card {
      .imgTaker {
        border-radius: 20px;
      }
      .text {
        display: none;
        h3 {
          font-size: 20px;
        }
        p {
          font-size: 12px;
          line-height: 20px;
        }
      }
      .links {
        font-size: 14px;
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }
}
